import { Component } from "@angular/core";
import { Router } from "@angular/router";
import { Card } from "src/app/shared/modules/card/components/common/card.model";
import { AnalysisService } from "src/app/shared/services/analysis-management/analysis/analysis.service";

@Component({
  selector: "app-dashboard",
  templateUrl: "./dashboard.component.html",
  providers: [AnalysisService],
})
export class DashboardComponent {
  items: Card[] = [
    {
      counter: 0,
      url: "/analysis-management/analyses",
      icon: "science",
      subtitle: "التحاليل",
      loading: true,
    },
    {
      counter: 0,
      url: "/sample-management/samples",
      icon: {
        class: "fas fa-vials",
      },
      subtitle: "العينات",
    },
  ];

  /**
   * Constructor
   */
  constructor(
    private analysisService: AnalysisService,
    private router: Router
  ) {
    this.getAnlysesCount();
  }

  /**
   * Get nodes count
   */
  getAnlysesCount() {
    this.analysisService.getAnalysesCount({}).subscribe((res) => {
      this.items[0].counter = res.count;
      this.items[0].loading = false;
    });
  }

  /**
   * Navigate
   * @param card
   */
  navigate(card: Card) {
    if (card && card.url) {
      this.router.navigate([card.url]);
    }
  }
}
